import { store } from "react-notifications-component";
import "react-notifications-component/dist/theme.css";
import "animate.css";

export function ShowSuccessNotification(message) {
	store.addNotification({
		title: "Success",
		message: message,
		type: "success",
		insert: "top",
		container: "top-right",
		animationIn: ["animated", "fadeIn"],
		animationOut: ["animated", "fadeOut"],
		dismiss: {
			duration: 5000,
			onScreen: true,
		},
	});
}

export function ShowDangerNotification(message) {
	store.addNotification({
		title: "Danger",
		message: message,
		type: "danger",
		insert: "top",
		container: "top-right",
		animationIn: ["animated", "fadeIn"],
		animationOut: ["animated", "fadeOut"],
		dismiss: {
			duration: 5000,
			onScreen: true,
		},
	});
}
