import React, { useState } from "react";
import { FormGroup, Button } from "reactstrap";
import ProductValidaion from "../Validation/ProductValidaion";

function Product({ product, products, handleProductChange, next, back }) {
	const [errors, setErrors] = useState({});

	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-4 offset-lg-4 border px-5 pb-5">
				<legend>Please select a data option</legend>
				<FormGroup className="form-row">
					<select
						class="custom-select"
						defaultValue={product}
						onChange={handleProductChange}
					>
						<option>Open this select menu</option>
						{products.map((item, i) => (
							<option key={i} value={item}>
								{item}
							</option>
						))}
					</select>
					<div className="text-danger small mt-1 font-italic">
						{errors.product}
					</div>
				</FormGroup>
				<Button
					color="primary"
					block
					onClick={() => {
						setErrors(next(product, ProductValidaion));
					}}
				>
					Next
				</Button>
				<Button
					color="secondary"
					block
					onClick={() => {
						back();
					}}
				>
					Back
				</Button>
			</fieldset>
		</React.Fragment>
	);
}

export default Product;
