import React from "react";
// import useForm from "../Shared/useForm";
// import validate from "../Validation/contactUsVal";
import { Form, FormGroup, Input, Button, Label } from "reactstrap";
import BGI from "../Images/mesh.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope, faMobile } from "@fortawesome/free-solid-svg-icons";

const ContactUs = () => {
	// const data = {
	// 	name: "",
	// 	email: "",
	// 	message: "",
	// };
	// const { handleChange, handleSubmit, values, errors } = useForm(
	// 	data,
	// 	submit,
	// 	validate
	// );
	// function submit() {
	// 	console.log("form submitted");
	// }
	return (
		<React.Fragment>
			<div
				id="contactUs"
				style={{
					backgroundImage: `url(${BGI})`,
					backgroundSize: "cover",
					backgroundPosition: "top",
				}}
			>
				<div className="f-bg py-5 h-100">
					<p className="display-4 text-center my-5 mx-5 font-weight-bold">
						GET IN TOUCH
					</p>
					<div className="row mx-1 pt-5 justify-content-around">
						<div className="col-12 col-lg-5 d-flex align-items-center justify-content-center">
							<div className="mt-2">
								<div>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faMobile} />
										076 661 3279
									</p>
								</div>
								<div className="mb-5">
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										info@sbwldata.co.za
									</p>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										admin@sbwldata.co.za
									</p>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										support@sbwldata.co.za
									</p>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										accounts@sbwldata.co.za
									</p>
								</div>
							</div>
						</div>
						<div className="col-12 col-lg-5 d-flex align-items-center mb-5">
							<Form
								className="w-100 b-shadow p-5"
								// onSubmit={handleSubmit}
								noValidate
							>
								<FormGroup className="text-left">
									<Label className="font-weight-bold">Name</Label>
									<Input
										name="name"
										type="text"
										// value={values.name}
										// onChange={handleChange}
										placeholder="John"
										className="border-bottom "
									/>
									<div className="text-danger small mt-1 font-italic">
										{/* {errors.name} */}
									</div>
								</FormGroup>
								<FormGroup className="text-left">
									<Label className="font-weight-bold">Email</Label>
									<Input
										name="email"
										type="email"
										// value={values.email}
										// onChange={handleChange}
										placeholder="Someone@domain.com"
										className="border-bottom"
									/>
									<div className="text-danger small mt-1 font-italic">
										{/* {errors.email} */}
									</div>
								</FormGroup>
								<FormGroup className="text-left">
									<Label className="font-weight-bold">Message</Label>
									<textarea
										name="message"
										// value={values.message}
										// onChange={handleChange}
										className="form-control border-bottom"
										rows="3"
									></textarea>
									<div className="text-danger small mt-1 font-italic">
										{/* {errors.message} */}
									</div>
								</FormGroup>
								<Button type="submit" className="btn-block mt-4">
									SEND
								</Button>
							</Form>
						</div>
					</div>
				</div>
			</div>
		</React.Fragment>
	);
};

export default ContactUs;
