import React, { useState } from "react";
import { Input, FormGroup, Button } from "reactstrap";
import PersonalInfoValidation from "../Validation/PersonalInfoValidation";

function PersonalInfo({ personalInfo, handlePersonalChange, next }) {
	const [errors, setErrors] = useState({});

	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-4 offset-lg-4 border px-5 pb-5">
				<legend>Account Information</legend>
				<FormGroup className="mt-3">
					<Input
						name="name"
						type="text"
						defaultValue={personalInfo.name}
						onChange={handlePersonalChange}
						placeholder="Name"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.name ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.name}
					</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="surname"
						type="text"
						defaultValue={personalInfo.surname}
						onChange={handlePersonalChange}
						placeholder="Surname"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.surname ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.surname}
					</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="email"
						type="text"
						defaultValue={personalInfo.email}
						onChange={handlePersonalChange}
						placeholder="Email Address"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.email ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.email}
					</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="id"
						type="text"
						defaultValue={personalInfo.id}
						onChange={handlePersonalChange}
						placeholder="ID Number"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.id ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">{errors.id}</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="cellphone"
						type="text"
						defaultValue={personalInfo.id}
						onChange={handlePersonalChange}
						placeholder="Mobile Number"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.cellphone ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.cellphone}
					</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="password"
						type="password"
						defaultValue={personalInfo.password}
						onChange={handlePersonalChange}
						placeholder="Password"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.password ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.password}
					</div>
				</FormGroup>
				<FormGroup>
					<Input
						name="confirmPassword"
						type="password"
						defaultValue={personalInfo.confirmPassword}
						onChange={handlePersonalChange}
						placeholder="Confirm Password"
						className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
							errors.confirmPassword ? "is-invalid" : ""
						}`}
					/>
					<div className="text-danger small mt-1 font-italic">
						{errors.confirmPassword}
					</div>
				</FormGroup>
				<Button
					color="primary"
					block
					onClick={() => {
						setErrors(next(personalInfo, PersonalInfoValidation));
					}}
				>
					Next
				</Button>
			</fieldset>
		</React.Fragment>
	);
}

export default PersonalInfo;
