import React, { useState } from "react";
import { Button } from "reactstrap";
import {
	ShowSuccessNotification,
	ShowDangerNotification,
} from "../Shared/notification";
import * as emailjs from "emailjs-com";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";

function Summary({ back, personalInfo, deliveryInfo, product }) {
	const [loading, setLoading] = useState(false);

	const handleSubmit = () => {
		setLoading(true);
		// e.preventDefault();

		emailjs
			.sendForm(
				"smtp_server",
				"order_template",
				"order_form",
				"user_2NUlgzcOgBlL7JKjxQraj"
			)
			.then(() => {
				const message =
					"Your order has been placed. Please see your email for more information!";
				ShowSuccessNotification(message);
				setLoading(false);
			})
			.catch(() => {
				const message =
					"Oops. Something went wrong. Please ensure you are connected to the internet or contact administrator at admin@sbwldat.co.za";
				ShowDangerNotification(message);
				setLoading(false);
			});
	};
	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-6 offset-lg-3 border px-5 pb-5">
				<legend>Summary Information</legend>
				<table className="mb-5">
					<tr>
						<td>
							<b>Name: </b>
						</td>
						<td>{personalInfo.name}</td>
					</tr>
					<tr>
						<td>
							<b>Surname: </b>
						</td>
						<td>{personalInfo.surname}</td>
					</tr>
					<tr>
						<td>
							<b>ID Number: </b>
						</td>
						<td>{personalInfo.id}</td>
					</tr>

					<tr>
						<td>
							<b>Email: </b>
						</td>
						<td>{personalInfo.email}</td>
					</tr>
					<tr>
						<td>
							<b>Mobile Number: </b>
						</td>
						<td>{personalInfo.cellphone}</td>
					</tr>
					<tr>
						<td>
							<b>Data Package: </b>
						</td>
						<td>{product}</td>
					</tr>
					<tr>
						<td>
							<b>Delivery Address: </b>
						</td>
						<td>
							{deliveryInfo.street}, {deliveryInfo.suburb}, {deliveryInfo.town},{" "}
							{deliveryInfo.postCode}
						</td>
					</tr>
				</table>
				<Button
					color="success"
					block
					disabled={loading}
					onClick={() => {
						handleSubmit();
					}}
				>
					{loading ? <FontAwesomeIcon icon={faSpinner} spin /> : "Place Order"}
				</Button>
				<Button
					color="secondary"
					block
					onClick={() => {
						back();
					}}
				>
					Back
				</Button>
			</fieldset>
			<form id="order_form">
				<input type="text" name="email" value={personalInfo.email} hidden />
				<input type="text" name="name" value={personalInfo.name} hidden />
				<input type="text" name="surname" value={personalInfo.Surname} hidden />
				<input type="text" name="package" value={product} hidden />
			</form>
		</React.Fragment>
	);
}

export default Summary;
